
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import AlertModule from 'vux/src/plugins/alert/module'

import NoData from '../components/NoData.vue'
import Header from '../components/Header.vue'
import Footer from '../components/Footer.vue'
import axios from 'axios'

export default {
name: 'Headlines',
directives: {
TransferDom
},
components: {
NoData,
Header,
Footer,
Popup,
AlertModule,
},
data () {
return {
headlinesList: [],
showHeadlinesDetails: false,
headlinesDetails: {},
}
},
created: function () {
axios.get('https://www.easy-mock.com/mock/5d0b684896278014af027705/TodayHeadlines/headlinesList')
.then( res => {
this.headlinesList = res.data.data.list || [e];
})
.catch(function (error) {
console.log(error);
});
},
methods: {
changePraise (index) {
this.headlinesList[index].praise = !this.headlinesList[index].praise;
},
toHeadlinesDetails (data) {
this.headlinesDetails = data;
this.showHeadlinesDetails = true;
},
undoneShare () {
if ( sessionStorage.getItem('userid') ){
this.showUndonePrompt()
}
else {
this.showPrompt()
}
},

showPrompt () {
var that = this;
AlertModule.show({
title: '提示',
content: '请先登录！',
maskZIndex: 5000,
dialogTransition: "none",
});
},
showUndonePrompt () {
var that = this;
AlertModule.show({
title: '提示',
content: '该模块暂未完成！',
maskZIndex: 5000,
dialogTransition: "none",
});
},
}
}



